<template>
    <div class="dynamic-issue">
        <mu-appbar class="appbar navigator" title="发布动态">
            <mu-button slot="left" color="#fff" @click="$router.back()" icon>
                <mu-icon value="navigate_before"/>
            </mu-button>
            <mu-button slot="right" color="#fff" @click="handlerSubmit" flat>
                发布
            </mu-button>
        </mu-appbar>
        <!-- 动态发布 -->
        <div class="dynamic-issue-content scroll-wrapper">
            <!-- 输入框 -->
            <div class="input-wrapper border-1px-b">
                <mu-text-field v-model="content" placeholder="分享新鲜事 ..."
                               :rows="5"
                               :rows-max="8"
                               multi-line
                               full-width
                               solo/>
            </div>
            <!-- 输入框 -->
            <!-- 图片上传 -->
            <div class="picture-wrapper">
                <div class="picture" v-for="(item, index) in images" :key="index"
                     @click="handlerBrowse(index)"
                     :style="`background-image: url(${item})`">
                    <mu-button color="#f44336" @click.stop="onDeletePicture(item, index)" small icon>
                        <mu-icon value="delete"/>
                    </mu-button>
                </div>
                <mu-ripple class="picture border-1px" @click="handlerPicture" v-if="images.length < 9">
                    <mu-icon class="append" value="add" size="50"/>
                </mu-ripple>
            </div>
            <!-- 图片上传 -->
            <!--<div class="location-wrapper">-->
            <!--<div class="location">-->
            <!--<mu-icon value="location_on" size="15"/>-->
            <!--<span class="text">武汉段. 汉口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道口水道</span>-->
            <!--</div>-->
            <!--</div>-->
        </div>
        <!-- 动态发布 -->
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import {saveMoment} from "../../api/dynamic"
    import ImagePreview from "vant/lib/image-preview"
    import {uploadBase64} from "../../api/sys"
    import {isArray} from "../../types/index"
    import {getPictures, getBase64} from "../../common/js/cordova/camera"

    export default {
        name: "dynamicIssue",
        data() {
            return {
                content: "",
                images: [],
                defaultList: []
            }
        },
        activated() {
            this.content = ''
            this.defaultList = []
            this.images = []
        },
        methods: {
            /***
             * 点击选择图片
             */
            handlerPicture() {
                // this.$refs.file.click()
                getPictures(9, this.defaultList).then(images => {
                    this.images = []
                    this.defaultList = []

                    Array.prototype.map.call(images, item => {
                        this.defaultList.push(item.path)

                        getBase64(item).then(res => {
                            this.images.push(`data:image/jpeg;base64,${res.thumbnailBase64}`)
                        })
                    })
                })
            },
            /***
             * 提交
             */
            async handlerSubmit() {
                const loading = this.$loading()

                const data = {
                    content: this.content,
                    type: '004001',
                    mediaType: '003002',
                    status: '020002'
                }

                if (this.images.length) {
                    const images = await Promise.all(this.images.map(item => {
                        // return upload(item.file)
                        return uploadBase64(item)
                    }))

                    data.mediaPath = isArray(images).join(';')
                }

                await saveMoment(data)

                loading.close()

                this.$toast('发表成功！')

                this.$emit('refresh')

                this.$router.go(-2)
            },
            /***
             * 点击浏览图片
             */
            handlerBrowse(index) {
                ImagePreview({
                    images: this.images,
                    startPosition: index
                })
            },
            /***
             * 点击删除图片
             */
            onDeletePicture(item, index) {
                this.images.splice(index, 1)

                this.defaultList.splice(index, 1)
            }
        },
        components: {
            Navigate
        }
    };
</script>

<style lang="stylus" scoped>
    @import '../../common/stylus/variable.styl'
    @import "../../common/stylus/mixins.styl"

    .dynamic-issue
        padding-top 46px
        background-color #fff
        overflow hidden
        .appbar
            position absolute
            top 0
            left 0
            z-index 1
            width 100%
            height 46px
            box-shadow none
        >>> .mu-appbar-title
            display flex
            align-items center
            justify-content center
            line-height 46px
            color #fff
            font-size 15px
        .scroll-wrapper
            width 100%
            height 100%
            .input-wrapper
                padding 10px 15px
            // .location-wrapper
            //     position relative
            //     padding 0 15px
            //     height 26px
            //     .location
            //         position absolute
            //         display flex
            //         align-items center
            //         padding 0 15px
            //         max-width 80%
            //         height 26px
            //         font-size $font-size-sm
            //         border-radius 16px
            //         overflow hidden
            //         .text
            //             flex 1
            //             no-wrap()
</style>



